import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { NotificationService } from '../../services/notification.service';
import { FirebaseListObservable } from 'angularfire2/database';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';

import { Notification } from './../../models/notification.model';
import { State } from './../../reducers/notification.reducer';
import * as fromNotifications from './../../reducers/notification.reducer';
import * as notificationActions from './../../actions/notification.actions';

@IonicPage({
  name: 'notifications-list-page',
  segment: 'notifications'
})
@Component({
  selector: 'page-notifications-list',
  templateUrl: 'notifications-list.page.html',
})
export class NotificationsPage {

  /**
   * Notifications list.
   */
  public notificationsList$: Observable<Notification[]>;

  /**
   * Creates new NotificationsPage instance.
   */
  public constructor(
    public navParams: NavParams,
    public navCtrl: NavController,
    private store: Store<fromNotifications.ModuleState>
  ) { }

  public ionViewDidLoad() {
    this.notificationsList$ = this.store.select(fromNotifications.getUserNotifications);
    this.store.dispatch(new notificationActions.GetCurrentUserNotifications());
  }

}



